<script>
export default {
  data(){
    return{
      isActive:false,
      isActive1:false,
      data:[]
    }
  },
  methods:{
    sign_out(){
      localStorage.removeItem('token');
      this.$router.push('/login')
    }
  },
  mounted() {
    this.$http.get('/get-me/',{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`
      }
    })
        .then(res=>{
          this.data=res.data
        })
  }
}
</script>
